import { Component, OnInit } from '@angular/core';
import { LoginandregistrationService } from '../../services/loginandregistration.service';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})


export class LoginComponent implements OnInit {

  serverErrorMessagesEmail: string;

  constructor(private service: LoginandregistrationService,
    private formBuilder: FormBuilder,
    private _router: Router,
    private _activatedRoute: ActivatedRoute) { }
    
  LoginForm: FormGroup;
  ngOnInit() {

    // document.getElementById('navbar').style.display='none';
    // document.getElementById('footer').style.display='none';    
    // this.service.deleteToken();
    this.LoginForm = this.formBuilder.group({
      _id: [''],
      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      password: ['', [Validators.required]],
      confirmpassword: ['', [Validators.required]],
    })
  }
err=[];
err1=[]
  login() {
    this.sharing();
    this.service.loginUser(this.LoginForm.value).subscribe(
      res => {
        // console.log(res);
        this.service.setToken(res['token'])
        this.userLogedIn();
      },
      error => {
          console.log(error)
        this.serverErrorMessagesEmail = error.message;
      }
    )

  }

  userData: any;
  userLogedIn() {
    this.service.logedin().subscribe(
      res => {
        // console.log(res);
        this.userData = res;
        localStorage.setItem('name',this.userData.userName);
        console.log("name",this.userData.userName);
        this._router.navigateByUrl('/curd');
      },
      error => {
        this.serverErrorMessagesEmail = error.message;
      }

    )
  }

  sharing(){
    let v=123;
    this.service.sharing(v);
  }

}
